"use client";

import React, { useEffect, useRef, useState, useCallback } from "react";
import Link from "next/link";
import Image from "next/image";
import { usePathname } from "next/navigation";
import { useSidebar } from "../context/SidebarContext";

import {
  Settings,
  Package,
  ShoppingCart,
  Warehouse,
  FileText,
  Users,
  BarChart3,
  LogIn,
  ChevronDown,
} from "lucide-react";

/* ======================
   Types
====================== */
type NavItem = {
  name: string;
  icon: React.ReactNode;
  path?: string;
  subItems?: { name: string; path: string }[];
};

/* ======================
   Menu Data
====================== */
const menuItems: NavItem[] = [
  {
    name: "Settings",
    icon: <Settings className="w-5 h-5" />,
    subItems: [
      { name: "Users", path: "/users" },
      { name: "Company", path: "/company" },
      { name: "Branch", path: "/company/branch" },
    ],
  },
  {
    name: "Products",
    icon: <Package className="w-5 h-5" />,
    subItems: [
      { name: "Category", path: "/company/category" },
      { name: "Category Type", path: "/company/category-type" },
      { name: "Products", path: "/product" },
    ],
  },
  {
    name: "Sales",
    icon: <ShoppingCart className="w-5 h-5" />,
    subItems: [
      { name: "Customers", path: "/sales/customers" },
      { name: "Invoices", path: "/sales/invoices" },
      { name: "Sales List", path: "/sales/list" },
      { name: "Cancelled Sales", path: "/sales/cancelled" },
      { name: "Payments", path: "/sales/payments" },
    ],
  },
  {
    name: "Inventory",
    icon: <Warehouse className="w-5 h-5" />,
    subItems: [
      { name: "Stock", path: "/inventory/stock" },
      { name: "Receive Stock", path: "/inventory/receive" },
      { name: "Transfer Stock", path: "/inventory/transfer" },
      { name: "Adjustments", path: "/inventory/adjustment" },
    ],
  },
  {
    name: "Reports",
    icon: <BarChart3 className="w-5 h-5" />,
    subItems: [
      { name: "Sales Report", path: "/reports/sales" },
      { name: "Inventory Report", path: "/reports/inventory" },
      { name: "Purchase Report", path: "/reports/purchases" },
    ],
  },
  {
    name: "Authentication",
    icon: <LogIn className="w-5 h-5" />,
    subItems: [
      { name: "Sign In", path: "/signin" },
      { name: "Sign Up", path: "/signup" },
    ],
  },
];

/* ======================
   Component
====================== */
const AppSidebar: React.FC = () => {
  const pathname = usePathname();
  const { isExpanded, isMobileOpen, isHovered, setIsHovered } = useSidebar();

  const [openIndex, setOpenIndex] = useState<number | null>(null);
  const subMenuRefs = useRef<Record<number, HTMLDivElement | null>>({});
  const [subMenuHeight, setSubMenuHeight] = useState<Record<number, number>>({});

  const isActive = useCallback(
    (path: string) => pathname === path,
    [pathname]
  );

  useEffect(() => {
    menuItems.forEach((item, index) => {
      item.subItems?.forEach((sub) => {
        if (isActive(sub.path)) {
          setOpenIndex(index);
        }
      });
    });
  }, [pathname, isActive]);

  useEffect(() => {
    if (openIndex !== null && subMenuRefs.current[openIndex]) {
      setSubMenuHeight((prev) => ({
        ...prev,
        [openIndex]:
          subMenuRefs.current[openIndex]?.scrollHeight || 0,
      }));
    }
  }, [openIndex]);

  return (
    <aside
      className={`fixed top-0 left-0 z-50 h-screen bg-white dark:bg-gray-900 border-r
      border-gray-200 dark:border-gray-800 transition-all duration-300
      ${isExpanded || isHovered ? "w-[280px]" : "w-[90px]"}
      ${isMobileOpen ? "translate-x-0" : "-translate-x-full"}
      lg:translate-x-0`}
      onMouseEnter={() => !isExpanded && setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      {/* Logo */}
      <div className="flex items-center justify-center py-6">
        <Link href="/">
          <Image
            src="/images/logo/logo-icon.svg"
            alt="Logo"
            width={36}
            height={36}
          />
        </Link>
      </div>

      {/* Navigation */}
      <nav className="px-3">
        <ul className="space-y-1">
          {menuItems.map((item, index) => (
            <li key={item.name}>
              <button
                onClick={() =>
                  setOpenIndex(openIndex === index ? null : index)
                }
                className={`flex items-center w-full gap-3 px-3 py-2 rounded-lg
                text-sm font-medium transition
                ${
                  openIndex === index
                    ? "bg-blue-50 text-blue-600 dark:bg-blue-500/10"
                    : "text-gray-700 hover:bg-gray-100 dark:text-gray-400 dark:hover:bg-white/5"
                }`}
              >
                {item.icon}
                {(isExpanded || isHovered) && (
                  <span className="flex-1 text-left">
                    {item.name}
                  </span>
                )}
                {(isExpanded || isHovered) && (
                  <ChevronDown
                    className={`w-4 h-4 transition-transform ${
                      openIndex === index ? "rotate-180" : ""
                    }`}
                  />
                )}
              </button>

             {/* Submenu */}
{(isExpanded || isHovered) && (
  <div
    ref={(el) => {
      subMenuRefs.current[index] = el;
    }}
    className="overflow-hidden transition-all duration-300"
    style={{
      height:
        openIndex === index
          ? `${subMenuHeight[index] || 0}px`
          : "0px",
    }}
  >
    <ul className="ml-8 mt-1 space-y-1">
      {item.subItems?.map((sub) => (
        <li key={sub.path}>
          <Link
            href={sub.path}
            className={`block rounded-md px-3 py-1.5 text-sm
              ${
                isActive(sub.path)
                  ? "text-blue-600 bg-blue-50 dark:bg-blue-500/10"
                  : "text-gray-600 hover:bg-gray-100 dark:text-gray-400 dark:hover:bg-white/5"
              }`}
          >
            {sub.name}
          </Link>
        </li>
      ))}
    </ul>
  </div>
)}

            </li>
          ))}
        </ul>
      </nav>
    </aside>
  );
};

export default AppSidebar;
